@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  border: none;
  background: none;
  cursor: pointer;
}
button:focus {
  outline: 0;
}

body {
  /* background-color: rgb(234, 237, 237); */
  font-family: "Nanum Gothic", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.border {
  border: 1px solid rgba(0, 0, 0, 0.1);
  /* border-radius: 6px; */
}

.App {
  background-image: url("https://cdn.jsdelivr.net/gh/chinsun9/go-home-time@gh-pages/sarah-wayte-WVIkqpoKz1I-unsplash.jpg");
  height: 100vh;
  background-size: cover;

  display: flex;
  flex-direction: column;
  justify-content: center;

  color: white;
  background-position: center;
}

.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1 1;
  align-items: center;
}

.home > span {
  text-shadow: 0 0 black;

  font-weight: 600;
}

.home .wow {
  margin-top: 40px;
  background-color: rgba(0, 0, 0, 0.486);
  padding: 10px;
}

.timer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.timer__content {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  padding: 30px;
  font-size: 4em;
  font-weight: bolder;
  color: white;
  text-shadow: 0 0 black;
}

.timer__content.over {
  color: black;
}

.header {
  display: flex;
  align-items: center;
  background-color: rgba(250, 235, 215, 0.041);
  height: 60px;
  display: flex;
  justify-content: center;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin-top: auto;

  justify-content: space-between;
  padding: 0 10px;
}

.about {
  display: flex;
  justify-content: center;
  flex: 1 1;
  text-shadow: 0 0 2px black;
  width: 280px;
  margin: auto;
  flex-direction: column;
}

.about p {
  line-height: 1.5;
}

.about a {
  color: rgb(210, 234, 255);
  transition: 0.4s all ease;
}

.about a:hover {
  color: rgb(89, 178, 255);
}

.share {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  margin-top: 40px;

  min-width: 280px;
}

.share__text input {
  padding: 10px;
  font-size: small;
  width: 100%;
  outline: none;
  border: none;
  border-radius: 6px;
  box-shadow: 0 0 1px black;
}

.share__checkbox {
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  min-height: 40px;
}

.share__checkbox label {
  display: flex;
  align-items: center;
}

.share__checkbox span {
  margin-left: 10px;
  font-size: smaller;
}

.share__checkbox input {
  opacity: 0.9;
  outline: none;
  border: none;
  padding: 3px;
  border-radius: 6px;
  box-shadow: 0 0 1px black;
  font-family: inherit;
}

.share__text {
  position: relative;
}

.share__modal {
  display: flex;
  opacity: 0;
  position: absolute;
  align-items: center;
  justify-content: center;
  top: -12px;
  width: 100%;
  pointer-events: none;
}

.share__modal span {
  padding: 20px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.575);
}

